@use "global" as *;


.section-title01 {
  display: inline-block;
  color: #7B695A;
  leading-trim: both;
  text-edge: cap;
  font-family: $font-family-en-1;
  font-size: rem(31);
  font-weight: $bold;
  line-height: 1.4;
  letter-spacing: 0.04em;
  text-indent: 0.04em;
  text-transform: capitalize;
  position: relative;

  padding-bottom: rem(20);
  margin: 0;
}

.section-title01::after {  // 下線
  content: "";
  display: block;
  width: rem(80);
  height: 1px;
  background: #7B695A;
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
}
