@use "global" as *;


.about {
  padding-top: rem(96);
  padding-bottom: rem(106);
  background: $color-background-base-1;
  position: relative;

  min-height: rem(754);
}

.about__img-left {
  width: fit-content;
  position: absolute;
  bottom: 0;
  left: 0;

  border-radius: 0px rem(119) 0px 0px;
  overflow: hidden;
}

.about__img-left img {
  aspect-ratio: 180 / 200.875;
  // width: rem(180);
  width: calc(180px / 1440px * 100vw);
  max-width: rem(180);
  object-fit: cover;
}

.about__img-right {
  width: fit-content;
  position: absolute;
  top: 0;
  right: 0;

  border-radius: 0px 0px 0px rem(305.5);
  overflow: hidden;
}

.about__img-right img {
  aspect-ratio: 628.9 / 661.36;
  // width: rem(628.9);
  width: calc(628.9px / 1440px * 100vw);
  max-width: rem(628.9);
  object-fit: cover;
}

.about__inner {

}

.about__wrap {
  width: fit-content;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  // margin-left: rem(-10);  // 位置調整
  position: absolute;
  top: rem(96);
  left: calc(50% - min(42vw, 33.75rem));  // *位置を制御
}

.about__title {

}

.about__text01 {
  margin-top: rem(30);
  text-align: center;
  leading-trim: both;
  text-edge: cap;
  font-family: $font-family-ja-1;
  font-size: rem(32);
  font-weight: $bold;
  line-height: 1.5;
}

.about__text02 {
  margin-top: rem(30);
  text-align: center;
  leading-trim: both;
  text-edge: cap;
  font-size: rem(16);
  line-height: 2.0;
}

.about__btn-wrap {

}

.about__btn {
  margin-top: rem(40);
  min-width: rem(251);
}
