@use "global" as *;


.btn {
  display: inline-block;
  text-align: center;
  leading-trim: both;
  text-edge: cap;
  font-family: $font-family-en-1;
  font-size: rem(20);
  font-weight: $bold;
  line-height: 1.4;
  letter-spacing: 0.04em;
  text-indent: 0.04em;
  text-transform: capitalize;

  padding-block: rem(24);
  padding-inline: rem(15);  // 最低限を指定
  border: 1px solid #000;
  transition: all 0.2s ease-out;
}

.btn:hover {  // hover
  color: $color-background-base-1;
  background: #000;
  border: 1px solid #000;
}
